h1 {
  text-align: center;
  color: #FFFFFF;
}

main {
  margin: 16px;
}

.capitalComponent,
.flagComponent {
  background: #FFFFFF;
  border-radius: 24px;
  margin-top: 60px;
  padding: 16px;
}

fieldset,
.bg {
  border-color: transparent;
}

form {
  display: flex;
  flex-direction: column;

  button {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: rgba(96, 102, 208, 0.8);
    border: 2px solid rgba(96, 102, 208, 0.7);
    box-sizing: border-box;
    border-radius: 12px;
    margin-top: 32px;
  }
}

button {
  padding-top: 14px;
  padding-bottom: 15px;
}

.greenBg {
  background-color:'#81C784';
}

.redBg {
  background-color: '#FF8A65'
}

.nextBtn {
  background: #F9A826;
  box-shadow: 0px 2px 4px rgba(252, 168, 47, 0.4);
  border-radius: 12px;
  padding-left: 37px;
  padding-right: 37px;
}

.showNextBtn {
  display: block;
}

.hideNextBtn {
  display: none;
}
